<template>
    <div>
        <div class="inner">
            <div class="center">
                <div style="height:50px"></div>
                <!--  -->
                <div>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/purchase' }"></el-breadcrumb-item>
                        <el-breadcrumb-item>{{purchaseDetails.projectName}}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div class="purchaseDetails">
                    <h3 class="projectName">{{purchaseDetails.projectName}}</h3>
                    <p class="fu">
                        <span class="projectDate">:{{purchaseDetails.projectDate}}</span>
                        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
                        <span class="tenderReference">:{{purchaseDetails.tenderReference}}</span>
                    </p>
                    <hr>
                    <div class="fu2">
                        <p>:{{purchaseDetails.tenderer}}</p>
                        <p>:{{purchaseDetails.projectArea}}</p>
                        <p>:{{purchaseDetails.deadline}}</p>
                        <p>:{{purchaseDetails.address}}</p>
                        <p>:{{purchaseDetails.agency}}</p>
                        <p>:{{purchaseDetails.tenderFile}}</p>
                        <p>:{{purchaseDetails.keyInfo}}</p>
                         <p>:{{purchaseDetails.linkerman}}</p>
                         <p>:{{purchaseDetails.tel}}</p>
                         <p>:{{purchaseDetails.mail}}</p>
                         <p>:{{purchaseDetails.tenderMan}}</p>
                         <p>:{{purchaseDetails.winningBidder}}</p>
                         <p>:{{purchaseDetails.assess}}</p>
                    </div>
                    
                </div>
                <div style="height:50px"></div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            //
            purchaseDetails:{},
        }
    },
    created(){
        this.for_purchaseDetails()
    },
    methods:{
        //
        for_purchaseDetails(){
            let url=`${this.$store.state.ipfg}/project/findOne/${this.$route.query.data}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.purchaseDetails=resp.data.data
                console
            })
        }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 0px auto 0px;
    /* background: darkmagenta */
    /* border: #383838 1px solid; */
    padding: 50px;
}

.center{
    position: relative;
}
.purchaseDetails{
    position: relative;
}
.purchaseDetails>.fu{
    text-align: center;
    line-height: 30px;
}
.purchaseDetails>.fu2{
    line-height: 25px;
}
.purchaseDetails>.projectName{
    text-align: center;
    line-height: 50px;
}
.purchaseDetails>.fu>.projectDate{
    font-size: 14px;
    color: #383838;
}
.purchaseDetails>.fu>.tenderReference{
    font-size: 14px;
    color: #383838;
}
</style>



