<template>
    <div>
        <div>
            <h3 class="pru"></h3>
            <hr>
            <div   class="lengthways"><div   class="img_size">
                <img   src="./delete_img/xinxifabuzhuji.png" alt="" @click="jup">
                </div>
                 <div   class="font">
                     <p  >
                     <span   class="title" @click="jup"></span>
                      <span   class="particular" @click="jup"></span>
                      </p>
                       <p   class="introduction">
                           <span  >:</span> 
                           <span  >
                        	
                        	Intel 7 i5 8400 3.1GHz
                        	32GB DDR4 2400
                        	1T
                        	
                        	24 16919201080IPS
                        	1000Mbps
                        	Windows10
                        Web	
                        	2U
                        	E5-2603V4 66 1.7
                        	64GB DDR420DIMM
                        	500G
                        	1000M*2
                        	
                        	24
                        	
                        	2U
                        	E5-2650V2CPU 82.6
                        	16GB DDR420DIMM
                        	500G2TB
                        	1000M*2
                        	
                        	24
                </span>
                </p>
                </div>
                </div>
            
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //
            list:[]
        }
    },
    watch: {
        $route(){
            //
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//
    },
    methods: {
        //
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/informationPublishingHost'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /*  */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



