<template>
    <div>
        <div>
            <h3 class="pru"></h3>
            <hr>
           <div class="lengthways">
               <div class="img_size">
                   <img src="./delete_img/lcdping.png" alt="" @click="jup">
                   </div>
                    <div class="font"><p><span class="title" @click="jup">LCD</span>
                     <span class="particular" @click="jup"></span>
                     </p> 
                     <p class="introduction">
                         <span>:</span>
                          <span>
                        	
                        	LED
                        	A
                        	16:9
                        	55
                        	178
                        2*15W	
                        	1920*1080
                        	1 HDMI (HDMI )
                        	USB*2USB*1HDMI*2RJ45*1*1TV*1AV*1 RS232*1VGA*1
                        	7H,89%,2%-5%
                        	
                        	HID
                        	
                        	32768*32768
                        	0.1mm
                        	USB
                        	1
                        2 PC 
                </span></p></div></div>
            
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //
            list:[]
        }
    },
    watch: {
        $route(){
            //
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//
    },
    methods: {
        //
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/LCDscreen'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /*  */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



