.shisi dl dd{width:92%; margin-top:4%; background:#fff; height:275px; font-size:12px; padding-left:4%; padding-right:4%; padding-top:15px; line-height:20px; letter-spacing:2px; color:#545454;}
.bigImg2 img{ max-width:870px;}

