<template>
    <div>
        <div>
            <h3 class="pru"></h3>
            <hr>
            <div   class="lengthways">
                <div   class="img_size">
                    <img   src="./delete_img/dianzibanpai.png" alt="" @click="jup">
                    </div>
                     <div   class="font">
                         <p  >
                             <span   class="title" @click="jup"></span>
                              <span   class="particular" @click="jup"></span>
                              </p>
                               <p   class="introduction">
                                   <span  >:</span> 
                                   <span  >
                        	
                        	1.8GHz 2G/8G
                        	DC12V*1USB2.0*1RJ45*1
                        	Android 5.1
                        	52704 (H) x 29646(V)mm
                        	178  (H) / 178  (V)
                        	 23.8
                        	1920*1080pixels
                        	A /A /A+,A 
                        	 450 cd/
                        	
                        	1000:1
                        	
                        	660mm(H) x 360mm(W) x 275mm(D)
                        	 2*3W 8
                        	DC 12V
                        	60W
                        	0.5W
                        	20ms
                        	724H
                        	
                        	660*360*27.5mm2mm:7.5kg0.5kg
                        	
                        	LOGO
                        LED A 4mm 92%	
                        1 DC IN1 1 USB 2.0Wifi 	
                        10	
                        IP	
                </span>
                </p>
                </div>
                </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //
            list:[]
        }
    },
    watch: {
        $route(){
            //
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//
    },
    methods: {
        //
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/electronicClassBrand'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /*  */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



